import React from 'react';
import Wave from 'react-wavify'
import { CDBFooter, CDBFooterLink, CDBBtn, CDBIcon, CDBContainer,CDBBox} from 'cdbreact';

export const Footer = () => {
  return (
    <>
     <Wave fill='#black'
        paused={false}
        options={{
          height: 10,
          amplitude: 10,
          speed: 0.25,
          points: 3
        }}
  />
    <CDBFooter className="shadow" style={{backgroundColor:'black'}}>
      <CDBBox
        display="flex"
        justifyContent="between"
        alignItems="center"
        className="mx-auto py-4 flex-wrap"
        style={{ width: '80%' }}
      >
        <CDBBox display="flex" alignItems="center">
          <a href="/" className="d-flex align-items-center p-0 text-dark">
            <img
              alt="logo"
              src="data:image/png;base64,"
              width="30px"
            />
            <span className="ml-4 h5 mb-0 font-weight-bold" style={{marginLeft:'20px',color:'white'}}>KashiYatra</span>
            <span className="ml-4 h5 mb-0 font-weight-bold" style={{marginLeft:'20px',color:'white'}}>Why CA</span>
            <span className="ml-4 h5 mb-0 font-weight-bold" style={{marginLeft:'20px',color:'white'}}>Contact</span>
            <span className="ml-4 h5 mb-0 font-weight-bold" style={{marginLeft:'20px',color:'white'}}>Learn More</span>
           
          </a>
          
        </CDBBox>
        <CDBBox display="flex">
          <CDBBtn flat color="dark" className="p-2">
            <CDBIcon fab icon="facebook-f" />
          </CDBBtn>
          <CDBBtn flat color="dark" className="mx-3 p-2">
            <CDBIcon fab icon="twitter" />
          </CDBBtn>
          <CDBBtn flat color="dark" className="p-2">
            <CDBIcon fab icon="instagram" />
          </CDBBtn>
        </CDBBox>
      </CDBBox>
    </CDBFooter>
    </>
    
  );
};